@import url('./__add-part/profile__add-part.css');
@import url('./__add-button/profile__add-button.css');
@import url('./__add-icon/profile__add-icon.css');

.profile {
  width: 100%;
  display: flex;
  justify-content: space-between;
  margin-top: 40px;
}

@media screen and (min-width: 501px) and (max-width: 600px) {
  .profile {
    width: 100%;
    flex-direction: column;
    align-items: center;
  }
}

@media screen and (min-width: 425px) and (max-width: 500px) {
  .profile {
    width: 100%;
    flex-direction: column;
    align-items: center;
  }
}

@media screen and (min-width: 320px) and (max-width: 424px) {
  .profile {
    width: 100%;
    flex-direction: column;
    align-items: center;
  }
}
